import React, { useEffect, useState } from "react";
import {
  Form,
  Select,
  InputNumber,
  Input,
  Button,
  Checkbox,
  Modal,
} from "antd";
import axiosInstance from "../../../actions/axios";
import { Link } from "react-router-dom";
import { updateTitle } from "../../common/UpdateTitle";

const { Option } = Select;

const createQuestionBank = () => {
  const [add_Bank_form] = Form.useForm();
  const [questionSets, setQuestionSets] = useState([]);
  // const [selectedqset, setSelectedqset] = useState([]);
  const [isPublished, setIsPublished] = useState(true);

  useEffect(() => {
    axiosInstance
      .get("/qbank/question-set/")
      .then((res) => {
        setQuestionSets(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
    updateTitle({
      title: "Create Question Bank",
    });
  }, [setQuestionSets]);

  function isPublishedhandler(e) {
    setIsPublished(e.target.checked);
  }
  const qBankSubmit_form = (value) => {
    const qbankBody = {
      name: value.title,
      description: value.description,
      price: value.price || 0,
      published: isPublished,
      subject: [],
      questionset: value.question_sets || [],
    };
    axiosInstance
      .post("/qbank/question-bank/", JSON.stringify(qbankBody))
      .then((res) => {
        Modal.success({
          title: "QuestionBank Added Succesfully",
          content: (
            <div>
              <h4>{res.data.name}</h4>
              <p>{res.data.description}</p>
            </div>
          ),
        });
        add_Bank_form.resetFields();
      })
      .catch((err) => {
        Modal.error({
          title: err.response.statusText,
          content: (
            <div>
              <p>Something went wrong. Contact to Administrator</p>
            </div>
          ),
        });
      });
  };
  return (
    <Form
      form={add_Bank_form}
      initialValues={{
        price: 0,
      }}
      onFinish={qBankSubmit_form}
    >
      <div className="pt-32pt pt-sm-64pt">
        <div className="container page__container pt-32pt pt-sm-64pt d-flex flex-column flex-md-row align-items-center text-center text-sm-left">
          <div className="flex d-flex flex-column flex-sm-row align-items-center">
            <div className="mb-24pt mb-sm-0 mr-sm-24pt">
              <h2 className="mb-0">Add Question Bank</h2>

              <ol className="breadcrumb p-0 m-0">
                <li className="breadcrumb-item">
                  <Link to="/">Home</Link>
                </li>

                <li className="breadcrumb-item active">Add Question bank</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <div className="page-section border-bottom-2">
        <div className="container page__container">
          <div className="row">
            <div className="col-md-8">
              <div className="page-separator">
                <div className="page-separator__text">Basic Information</div>
              </div>
              <label className="form-label" htmlFor="title">
                Question Bank Title
              </label>
              <div className="form-group mb-24pt">
                <Form.Item name="title">
                  <Input
                    className="form-control form-control-lg"
                    placeholder="Title..."
                  />
                </Form.Item>
              </div>
              <div className="form-group">
                <label className="form-label" htmlFor="description">
                  Description
                </label>
                <Form.Item name="description" noStyle>
                  <Input.TextArea
                    placeholder="Description"
                    rows="8"
                  ></Input.TextArea>
                </Form.Item>
              </div>
              <div className="form-group mb-32pt">
                <Checkbox onChange={isPublishedhandler} checked={isPublished}>
                  Published
                </Checkbox>
              </div>
            </div>
            <div className="col-md-4">
              <div className="page-separator">
                <div className="page-separator__text">Options</div>
              </div>

              <div className="card">
                <div className="card-body">
                  <div className="form-group">
                    <label className="form-label" htmlFor="question_sets">
                      Question Set
                    </label>
                    <Form.Item name="question_sets">
                      <Select
                        mode="multiple"
                        allowClear
                        style={{ width: "100%" }}
                        placeholder="Please select Question Sets"
                      >
                        {questionSets.map((qset) => (
                          <Option
                            key={qset.id}
                            title={qset.name}
                            value={qset.id}
                          >
                            {qset.name}
                          </Option>
                        ))}
                      </Select>
                    </Form.Item>
                    <small className="form-text text-muted">
                      Select one or more Question Set.
                    </small>
                  </div>

                  <div className="form-group">
                    <label className="form-label" htmlFor="price">
                      Price
                    </label>
                    <div className="input-group">
                      <Form.Item name="price" noStyle>
                        <InputNumber className="mr-1" min={0} max={10000} />
                      </Form.Item>
                    </div>
                    <small className="form-text text-muted">
                      Question Bank Price in Taka
                    </small>
                  </div>
                </div>
              </div>
            </div>
            <div className="form-group">
              <Form.Item>
                <Button
                  type="primary"
                  className="btn btn-accent mb-24pt mb-sm-0"
                  htmlType="submit"
                >
                  Add QuestionBank
                </Button>
              </Form.Item>
            </div>
          </div>
        </div>
      </div>
    </Form>
  );
};

export default createQuestionBank;
